import React, { Component } from 'react';

export default function({children, category}) {
  const w = encodeURIComponent(children);
  const c = encodeURIComponent(category);
  let q = w;
  if (category !== '') {
    q = `${w}+${c}`;
  }
  return <a target={encodeURIComponent(children)} href={`https://www.google.com/search?q=${q}`}>{children}</a>
}

export const H3Link = function({children, category}) {
  const w = encodeURIComponent(children);
  const c = encodeURIComponent(category);
  let q = w;
  if (category !== '') {
    q = `${w}+${c}`;
  }
  return <h3>□ <a target={encodeURIComponent(children)} href={`https://www.google.com/search?q=${q}`}>{children}</a></h3>
}
